import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import {
  Layout, Form, Row, Col, Button, Spin, Input, Alert,
  DatePicker, Switch, Select, InputNumber,
} from "antd";
import * as Coms from "@/components";
import moment from "moment";
import { permissions, queryToObj, dataType, tableName } from "@/components/utils/fun";
import Apis from "@/apis";
const { Content } = Layout;
const { TextArea } = Input;
const fullWidthComs = ['EditTable']

@inject(({ globalStore, appStore, authStore }) => ({
  ...globalStore, ...appStore, ...authStore
}))
@observer
class EditView extends Component {
  formRef = React.createRef();
  id = ''
  funsMap = {}
  state = {
    loading: true,
    actions: [],
    formData: {},
    showResult: false,
    resultStatus: true,
    resultMsg: "",
    resultTitle: '',
  };

  componentDidMount() {
    this.id = this.props.id
    this.initData()
  }

  initData() {
    const { items, action = "", tbName, urlQuery } = this.props;
    let actions = action.split(",") || [];
    this.setState({ actions });
    Apis.appList({ type: 'form_data', tbName: tableName(tbName) }, 'customize-funs').then(res => {
      res.forEach(item => {
        item.queryFields.forEach(_item => {
          this.funsMap[_item.value] = this.funsMap[_item.value] || {}
          this.funsMap[_item.value][item.fromTable] = item
        })
      })
    })
    if (this.id) {
      Apis.appDetail({ _id: this.id, ...queryToObj(urlQuery) }, tableName(tbName)).then(
        (result) => {
          if (result) {
            items.forEach(item => {
              if (item.component === 'DatePicker') {
                result[item.name] = moment(result[item.name])
              }
            })
            this.setState({
              formData: result,
              loading: false,
            }, () => this.formRef.current.setFieldsValue(result));
          }
        }
      );
    } else {
      this.setState({ loading: false }, () => {
        if (this.formRef.current) this.formRef.current.resetFields();
      });
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { id } = nextProps
    if (id !== this.id) {
      this.id = id
      this.initData();
    }
  }

  handleSubmit = (values) => {
    const { id, tbName, onChange } = this.props;
    this.handleResetForm(values);
    Apis[id ? "appUpdate" : "appCreate"](
      {
        _id: id,
        ...values,
      },
      tableName(tbName)
    ).then(res => {
      if (onChange) onChange()
      if (res && !res.status) {
        // message.success('!')
        this.setState({ showResult: true, resultStatus: true });
      } else {
        let obj = {
          showResult: true,
          resultStatus: false,
        }
        if (res.error && res.message) {
          obj.resultTitle = res.error
          obj.resultMsg = res.message
        }
        this.setState(obj);
      }
    });
  };
  // 
  handleInput(e, render) {
    // console.warn(e);
    if (e.target && e.target.localName === "input" && render) {
      const { execFun } = this.props;
      let formData = this.formRef.current.getFieldsValue();
      let value = execFun(render, e.target.value, formData);
      if (value !== null) {
        formData[e.target.id] = value;
        this.formRef.current.setFieldsValue(formData);
        if (value.length && value !== "undefined" && value !== "null") {
          e.target.value = value;
        }
      }
    }
  }

  async handleChange(e, name) {
    if (name && this.funsMap[name]) {
      let formData = this.formRef.current.getFieldsValue();
      for (let tbName of Object.keys(this.funsMap[name])) {
        let item = this.funsMap[name][tbName]
        let params = {}
        item.queryFields.forEach(query => {
          let value = formData[query.value]
          if (value !== undefined && value !== '') params[query.key] = dataType(value, query.type)
        })
        if (item.queryFields.length === Object.keys(params).length && item.resultFields.length) {
          let res = await Apis.appList(params, tableName(tbName)) || [];
          // console.warn(res);
          if (Array.isArray(res) && res.length) {
            let data = {}
            item.resultFields.forEach(result => {
              data[result.value] = dataType(res[0][result.key], result.type)
            })
            this.formRef.current.setFieldsValue(data);
          }
        }
      }
    }
  }

  getComponent(item) {
    let { component, comProps = {}, render, selectOptions, name, label } = item;
    let Com = Coms[component] || Input, props = {};
    if (component === "Input") {
      Com = Input;
    } else if (component === "TextArea") {
      Com = TextArea;
      props.rows = 4
    } else if (component === 'DatePicker') {
      Com = DatePicker
      props.showTime = true
    } else if (component === 'InputNumber') {
      props.min = 0
      Com = InputNumber
    } else if (component === 'Switch') {
      Com = Switch
    } else if (component === 'Select') {
      Com = Select
      props.options = selectOptions ? selectOptions.options.map(({ key, value }) => ({ label: key, value })) : []
    }
    return (
      <Com
        onFocus={e => this.handleInput(e, render)}
        onInput={e => this.handleInput(e, render)}
        onBlur={e => this.handleChange(e, name)}
        title={label}
        {...comProps}
        {...props}
      />
    );
  }
  // 
  getRules(name) {
    const { rules } = this.props;
    let _rules = rules.filter((item) => item.name === name);
    if (_rules.length) {
      return _rules.map((item) => {
        let _data = {};
        let { name, id, _id, __v, ...data } = item;
        for (let key in data) {
          if (data[key] !== null && data[key] !== undefined)
            _data[key] = data[key];
        }
        return _data;
      });
    } else {
      return [];
    }
  }

  getItem = item => {
    let props = {}
    if (item.component === 'Switch') {
      props.valuePropName = 'checked'
    }
    if (item.component === 'InputNumber' && item.unit) { //
      return (
        <Form.Item label={item.label || " "}>
          <Form.Item
            name={item.name}
            rules={this.getRules(item.name)}
            help={item.help || undefined}
            key={`item_${item.name}`}
            {...props}
            noStyle
          >
            {this.getComponent(item)}
          </Form.Item>
          <span className="ant-form-text"> {item.unit}</span>
        </Form.Item>
      )
    } else if (fullWidthComs.includes(item.component)) {
      return (
        <Form.Item label={""}>
          <Form.Item
            name={item.name}
            rules={this.getRules(item.name)}
            help={item.help || undefined}
            key={`item_${item.name}`}
            {...props}
            noStyle
          >
            {this.getComponent(item)}
          </Form.Item>
        </Form.Item>
      )
    } else {
      return (
        <Form.Item
          label={item.label || " "}
          name={item.name}
          rules={this.getRules(item.name)}
          help={item.help || undefined}
          key={`item_${item.name}`}
          {...props}
        >
          {this.getComponent(item)}
        </Form.Item>
      )
    }
  };

  getFields = () => {
    const { items = [], pageLayout } = this.props;
    let children = [];
    if (pageLayout === "A") {
      children = items.map((item, i) => {
        return (
          <Col
            span={fullWidthComs.includes(item.component) ? 24 : 8}
            key={`item_${i}`}>
            {this.getItem(item)}
          </Col>
        )
      });
    }
    if (pageLayout === "B") {
      children = items.map((item, i) => this.getItem(item));
    }
    return children;
  };

  handleResetForm(values) {
    const { items = [] } = this.props;
    let { formData } = this.state,
      result = {};
    items.forEach((item) => {
      result[item.name] = item.resetEnable
        ? formData[item.name]
        : values[item.name];
    });
    this.setState({ formData: result });
  }

  handleGoOn() {
    this.setState({ showResult: false });
  }

  render() {
    const { tbName, formItemLayout, formTailLayout, pageLayout, formLayout, } = this.props;
    const { formData, showResult, resultStatus, resultMsg, actions, resultTitle, } = this.state;
    const labelCol = {
      xs: { span: 24 },
      sm: { span: 8 },
    };
    return tbName ? (
      <Content>
        {(permissions(tbName, `create`) || permissions(tbName, `update`)) &&
          (actions.includes("update") || actions.includes("create")) ? (
          <div className="app-content-div">
            {!showResult ? (
              <Spin tip="..." spinning={this.state.loading}>
                <Form
                  {...(pageLayout === "B" ? formItemLayout : { labelCol })}
                  layout={formLayout}
                  onFinish={this.handleSubmit}
                  initialValues={formData}
                  ref={this.formRef}
                  // name={name}
                  className="app-form"
                >
                  {pageLayout === "A" ? (
                    <>
                      <Row gutter={24}>{this.getFields()}</Row>
                      <Row gutter={24}>
                        <Col span={24} style={{ textAlign: "center" }}>
                          <Form.Item style={{ margin: 0 }}>
                            <Button
                              type="primary"
                              htmlType="submit"
                              style={{ marginRight: 10 }}
                            >
                              
                            </Button>
                            <Button
                              type="default"
                              onClick={() => this.setState({ formData: {} }, () => this.formRef.current.resetFields())}
                              style={{ marginRight: 10 }}
                            >
                              
                            </Button>
                          </Form.Item>
                        </Col>
                      </Row>
                    </>
                  ) : null}
                  {pageLayout === "B" ? (
                    <>
                      {this.getFields()}
                      <Form.Item {...formTailLayout}>
                        <Button
                          type="primary"
                          htmlType="submit"
                          style={{ marginRight: 10 }}
                        >
                          
                        </Button>
                        <Button
                          type="default"
                          onClick={() => this.setState({ formData: {} }, () => this.formRef.current.resetFields())}
                          style={{ marginRight: 10 }}
                        >
                          
                        </Button>
                      </Form.Item>
                    </>
                  ) : null}
                </Form>
              </Spin>
            ) : (
              <Coms.Result
                title={resultTitle}
                status={resultStatus}
                msg={resultMsg}
                onClose={false}
                onGoto={() => this.handleGoOn()}
              />
            )}
          </div>
        ) : (
          <Alert
            message=""
            description=", ."
            type="warning"
            showIcon
            style={{ margin: "0 20px" }}
          />
        )}
      </Content>
    ) : (
      <div className="app-spin">
        <Spin />
      </div>
    );
  }
}

export default EditView;
